import { useEffect, useState } from 'react';
import type { DatabaseDto } from '@chartsPage/metaData/shared/dtos/DatabaseDto';
import type { CreateDatabaseRequest } from '@chartsPage/metaData/shared/dtos/requests/CreateDatabaseRequest';
import type { UpdateDatabaseRequest } from '@chartsPage/metaData/shared/dtos/requests/UpdateDatabaseRequest';
import type { TestConnectionResponse } from '@chartsPage/metaData/shared/dtos/responses/TestConnectionResponse';
import { DbProviderType } from '@chartsPage/metaData/shared/dtos/types/DbProviderType';
import { useTestConnectionMutation } from '@chartsPage/metaData/shared/api/metadataApi';
import styles from './DatabaseEditModal.module.css';

interface DatabaseEditModalProps {
    readonly database: DatabaseDto | null; // null = создание новой
    readonly onCancel: () => void;
    readonly onSave: (data: CreateDatabaseRequest | UpdateDatabaseRequest) => void;
    readonly isLoading?: boolean;
}

export function DatabaseEditModal({ database, onCancel, onSave, isLoading }: DatabaseEditModalProps) {
    const isEdit = database !== null;

    const [name, setName] = useState('');
    const [connectionString, setConnectionString] = useState('');
    const [databaseVersion, setDatabaseVersion] = useState('');
    const [provider, setProvider] = useState<DbProviderType>(DbProviderType.Postgres);

    // Test connection state
    const [testConnection, { isLoading: isTesting }] = useTestConnectionMutation();
    const [testResult, setTestResult] = useState<TestConnectionResponse | null>(null);

    useEffect(() => {
        if (database) {
            setName(database.name);
            setConnectionString(database.connectionString);
            setDatabaseVersion(database.databaseVersion ?? '');
            setProvider(database.provider);
        } else {
            setName('');
            setConnectionString('');
            setDatabaseVersion('');
            setProvider(DbProviderType.Postgres);
        }
        setTestResult(null);
    }, [database]);

    // Clear test result when connection string or provider changes
    useEffect(() => {
        setTestResult(null);
    }, [connectionString, provider]);

    // Закрытие по ESC
    useEffect(() => {
        const onKey = (e: KeyboardEvent) => {
            if (e.key === 'Escape') onCancel();
        };
        document.addEventListener('keydown', onKey);
        const prev = document.body.style.overflow;
        document.body.style.overflow = 'hidden';
        return () => {
            document.removeEventListener('keydown', onKey);
            document.body.style.overflow = prev;
        };
    }, [onCancel]);

    const handleTestConnection = async () => {
        if (!connectionString.trim()) return;

        try {
            const result = await testConnection({
                connectionString: connectionString.trim(),
                provider,
            }).unwrap();
            setTestResult(result);

            // If test successful and version is empty, fill it from response
            if (result.success && result.serverVersion && !databaseVersion.trim()) {
                setDatabaseVersion(result.serverVersion);
            }
        } catch (error) {
            setTestResult({
                success: false,
                serverVersion: null,
                errorMessage: 'Не удалось выполнить запрос',
                responseTimeMs: 0,
            });
        }
    };

    const handleSubmit = () => {
        if (!name.trim() || !connectionString.trim()) return;

        if (isEdit && database) {
            const updateData: UpdateDatabaseRequest = {
                id: database.id,
                name: name.trim(),
                connectionString: connectionString.trim(),
                databaseVersion: databaseVersion.trim(),
                provider,
            };
            onSave(updateData);
        } else {
            const createData: CreateDatabaseRequest = {
                name: name.trim(),
                connectionString: connectionString.trim(),
                databaseVersion: databaseVersion.trim(),
                provider,
            };
            onSave(createData);
        }
    };

    const isValid = name.trim() !== '' && connectionString.trim() !== '';
    const canTest = connectionString.trim() !== '';

    // Получаем все значения enum для select
    const providerOptions = Object.entries(DbProviderType)
        .filter(([key]) => isNaN(Number(key)))
        .map(([key, value]) => ({ label: key, value: value as DbProviderType }));

    return (
        <div className={styles.backdrop} onClick={onCancel} role="dialog" aria-modal="true">
            <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
                <div className={styles.modalHeader}>
                    <div className={styles.modalTitle}>
                        {isEdit ? 'Редактировать базу данных' : 'Создать базу данных'}
                    </div>
                    <button
                        className={styles.btnClose}
                        onClick={onCancel}
                        aria-label="Закрыть"
                        type="button"
                    >
                        ✕
                    </button>
                </div>

                <div className={styles.modalBody}>
                    <div className={styles.fieldRow}>
                        <label className={styles.fieldLabel}>
                            Название <span className={styles.required}>*</span>
                        </label>
                        <input
                            className={`${styles.input} ${!name.trim() ? styles.inputError : ''}`}
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            placeholder="Введите название"
                            disabled={isLoading}
                        />
                    </div>

                    <div className={styles.fieldRow}>
                        <label className={styles.fieldLabel}>Провайдер</label>
                        <select
                            className={styles.select}
                            value={provider}
                            onChange={(e) => setProvider(Number(e.target.value) as DbProviderType)}
                            disabled={isLoading}
                        >
                            {providerOptions.map((opt) => (
                                <option key={opt.value} value={opt.value}>
                                    {opt.label}
                                </option>
                            ))}
                        </select>
                    </div>

                    <div className={styles.fieldRow}>
                        <label className={styles.fieldLabel}>
                            Строка подключения <span className={styles.required}>*</span>
                        </label>
                        <textarea
                            className={`${styles.textarea} ${!connectionString.trim() ? styles.inputError : ''}`}
                            value={connectionString}
                            onChange={(e) => setConnectionString(e.target.value)}
                            placeholder="Host=...;Database=...;Username=...;Password=..."
                            rows={3}
                            disabled={isLoading}
                        />
                        <button
                            className={styles.btnTest}
                            onClick={handleTestConnection}
                            disabled={!canTest || isTesting || isLoading}
                            type="button"
                        >
                            {isTesting ? 'Проверка...' : 'Проверить подключение'}
                        </button>
                    </div>

                    {/* Test result */}
                    {testResult && (
                        <div className={testResult.success ? styles.testSuccess : styles.testError}>
                            {testResult.success ? (
                                <>
                                    <span className={styles.testIcon}>✓</span>
                                    <div className={styles.testMessage}>
                                        <strong>Подключение успешно</strong>
                                        {testResult.serverVersion && (
                                            <span>Версия сервера: {testResult.serverVersion}</span>
                                        )}
                                        <span>Время ответа: {testResult.responseTimeMs} мс</span>
                                    </div>
                                </>
                            ) : (
                                <>
                                    <span className={styles.testIcon}>✕</span>
                                    <div className={styles.testMessage}>
                                        <strong>Ошибка подключения</strong>
                                        {testResult.errorMessage && (
                                            <span>{testResult.errorMessage}</span>
                                        )}
                                        {testResult.responseTimeMs > 0 && (
                                            <span>Время ответа: {testResult.responseTimeMs} мс</span>
                                        )}
                                    </div>
                                </>
                            )}
                        </div>
                    )}

                    <div className={styles.fieldRow}>
                        <label className={styles.fieldLabel}>Версия БД</label>
                        <input
                            className={styles.input}
                            value={databaseVersion}
                            onChange={(e) => setDatabaseVersion(e.target.value)}
                            placeholder="Например: 15.0"
                            disabled={isLoading}
                        />
                    </div>

                </div>

                <div className={styles.modalFooter}>
                    <button
                        className={styles.btnSecondary}
                        onClick={onCancel}
                        disabled={isLoading}
                        type="button"
                    >
                        Отмена
                    </button>
                    <button
                        className={styles.btnPrimary}
                        disabled={!isValid || isLoading}
                        onClick={handleSubmit}
                        type="button"
                    >
                        {isLoading ? 'Сохранение...' : isEdit ? 'Сохранить' : 'Создать'}
                    </button>
                </div>
            </div>
        </div>
    );
}
