// src/features/chartsPage/charts/mirax/MiraxContainer/TechnicalRunsPanel/TechnicalRunsPanel.tsx
import { useMemo, useState, useCallback, type JSX, useEffect, useRef } from 'react';
import classNames from 'classnames';

import styles from './TechnicalRunsPanel.module.css';
import { useAppDispatch, useAppSelector } from '@/store/hooks';
import {
    selectCurrentDatabaseId,
    selectIsTechnicalRunsLoading,
    selectTechnicalRunsError,
    openTechnicalRunTab,
    selectActiveContextId,
    selectTechnicalRunsData,
    setActiveFactoryNumber,
} from '@chartsPage/mirax/miraxSlice';
import { fetchTechnicalRuns } from '@chartsPage/mirax/miraxThunks';
import { ErrorMessage } from '@chartsPage/mirax/MiraxContainer/ErrorMessage/ErrorMessage';
import { SortDropdown } from '@chartsPage/mirax/MiraxContainer/TechnicalRunsPanel/SortDropdown/SortDropdown';
import type { TechnicalRunDto } from '@chartsPage/mirax/contracts/TechnicalRunDto';
import {
    formatTechnicalRunDate,
    sortTechnicalRuns,
    TechnicalRunSortType,
    type TechnicalRunSortType as TechnicalRunSortTypeValue,
} from '@chartsPage/mirax/MiraxContainer/utils/miraxHelpers';
import { SearchInput } from '@chartsPage/mirax/MiraxContainer/SearchInput/SearchInput';
import { CopyButton } from '@chartsPage/components/CopyButton/CopyButton.tsx';
import { FactoryNumberSearch } from '@chartsPage/mirax/MiraxContainer/TechnicalRunsPanel/FactoryNumberSearch/FactoryNumberSearch';

export function TechnicalRunsPanel(): JSX.Element {
    const dispatch = useAppDispatch();
    const databaseId = useAppSelector(selectCurrentDatabaseId);
    const isLoading = useAppSelector(selectIsTechnicalRunsLoading);
    const error = useAppSelector(selectTechnicalRunsError);
    const activeContextId = useAppSelector(selectActiveContextId);

    const [searchQuery, setSearchQuery] = useState('');
    const [activeFactoryNumber, setActiveFactoryNumberLocal] = useState<string | undefined>(undefined);
    const [sortType, setSortType] = useState<TechnicalRunSortTypeValue>(
        TechnicalRunSortType.DATE_END_DESC
    );

    const technicalRuns = useAppSelector(selectTechnicalRunsData);

    // Отслеживаем для какой БД уже пытались загрузить данные (защита от повторных запросов при ошибке)
    const attemptedLoadForDbRef = useRef<string | undefined>(undefined);

    // Автозагрузка при смене БД
    useEffect(() => {
        // Нет БД - нечего загружать
        if (!databaseId) {
            attemptedLoadForDbRef.current = undefined;
            return;
        }

        // Уже загружаем - не дублируем
        if (isLoading) {
            return;
        }

        // Уже есть данные - не загружаем
        if (technicalRuns.length > 0) {
            attemptedLoadForDbRef.current = databaseId;
            return;
        }

        // Уже пытались загрузить для этой БД (и получили ошибку) - не повторяем автоматически
        if (attemptedLoadForDbRef.current === databaseId) {
            return;
        }

        // Запоминаем, что пытаемся загрузить для этой БД
        attemptedLoadForDbRef.current = databaseId;

        void dispatch(
            fetchTechnicalRuns({ databaseId })
        );
    }, [databaseId, dispatch, isLoading, technicalRuns.length]);

    const filteredAndSortedRuns = useMemo(() => {
        let result = technicalRuns;

        if (searchQuery.trim()) {
            const query = searchQuery.toLowerCase().trim();

            result = technicalRuns.filter((run) => {
                const nameMatch = typeof run.name === 'string' && run.name.toLowerCase().includes(query);
                const startDate = new Date(run.dateStartTime).toLocaleDateString('ru-RU');
                const endDate = new Date(run.dateEndTime).toLocaleDateString('ru-RU');
                const dateMatch = startDate.includes(query) || endDate.includes(query);

                return nameMatch || dateMatch;
            });
        }

        return sortTechnicalRuns(result, sortType);
    }, [technicalRuns, searchQuery, sortType]);

    const handleRetry = useCallback(() => {
        if (!databaseId) return;
        // Сбрасываем ref, чтобы разрешить повторную загрузку
        attemptedLoadForDbRef.current = undefined;
        void dispatch(
            fetchTechnicalRuns({
                databaseId,
                factoryNumber: activeFactoryNumber,
            })
        );
    }, [dispatch, databaseId, activeFactoryNumber]);

    const handleSearchChange = useCallback((value: string) => {
        setSearchQuery(value);
    }, []);

    const handleSearchClear = useCallback(() => {
        setSearchQuery('');
    }, []);

    const handleSortChange = useCallback((newSortType: TechnicalRunSortTypeValue) => {
        setSortType(newSortType);
    }, []);

    // НЕ ИЗМЕНЕНО: оставляем как было
    const handleRunClick = useCallback(
        (run: TechnicalRunDto) => {
            dispatch(
                openTechnicalRunTab({
                    id: run.id,
                    name: run.name,
                })
            );
        },
        [dispatch]
    );

    const handleFactoryNumberSearch = useCallback(
        (factoryNumber: string) => {
            if (!databaseId) return;
            setActiveFactoryNumberLocal(factoryNumber);
            dispatch(setActiveFactoryNumber(factoryNumber));
            // Сбрасываем ref для новой загрузки
            attemptedLoadForDbRef.current = undefined;
            void dispatch(
                fetchTechnicalRuns({
                    databaseId,
                    factoryNumber,
                })
            );
        },
        [dispatch, databaseId]
    );

    const handleFactoryNumberClear = useCallback(() => {
        if (!databaseId) return;
        setActiveFactoryNumberLocal(undefined);
        dispatch(setActiveFactoryNumber(undefined));
        // Сбрасываем ref для новой загрузки
        attemptedLoadForDbRef.current = undefined;
        void dispatch(
            fetchTechnicalRuns({
                databaseId,
            })
        );
    }, [dispatch, databaseId]);

    if (isLoading) {
        return (
            <div className={styles.containerTechnicalRunsPanel}>
                <div className={styles.loaderContainer}>
                    <div className={styles.spinner} />
                </div>
            </div>
        );
    }

    if (error !== undefined) {
        return (
            <div className={styles.container}>
                <ErrorMessage message={error} onRetry={handleRetry} />
            </div>
        );
    }

    const showNoResults = searchQuery.trim() && filteredAndSortedRuns.length === 0;

    return (
        <div className={styles.containerTechnicalRunsPanel}>
            <div className={styles.header}>
                <h2 className={styles.title}>Испытания</h2>
                <span className={styles.count}>
                    {searchQuery.trim()
                        ? `${filteredAndSortedRuns.length} из ${technicalRuns.length}`
                        : technicalRuns.length}
                </span>
            </div>

            <div className={styles.controls}>
                {activeFactoryNumber !== undefined && (
                    <div className={styles.activeFilterBadge}>
                        Фильтр: устройство <strong>{activeFactoryNumber}</strong>
                    </div>
                )}

                <FactoryNumberSearch
                    onSearch={handleFactoryNumberSearch}
                    onClear={handleFactoryNumberClear}
                    isActive={activeFactoryNumber !== undefined}
                />

                <SearchInput
                    value={searchQuery}
                    onChange={handleSearchChange}
                    onClear={handleSearchClear}
                    placeholder="Поиск по имени или дате..."
                />
                <SortDropdown value={sortType} onChange={handleSortChange} />
            </div>

            <div className={styles.list}>
                {technicalRuns.length === 0 ? (
                    <div className={styles.placeholder}>
                        {activeFactoryNumber !== undefined
                            ? `Не найдено испытаний для устройства ${activeFactoryNumber}`
                            : 'Нет доступных испытаний'}
                    </div>
                ) : showNoResults ? (
                    <div className={styles.placeholder}>
                        Ничего не найдено по запросу "{searchQuery}"
                    </div>
                ) : (
                    <ul className={styles.items}>
                        {filteredAndSortedRuns.map((run) => (
                            <li
                                key={run.id}
                                className={classNames(
                                    styles.item,
                                    activeContextId === run.id && styles.itemActive
                                )}
                                onClick={() => handleRunClick(run)}
                            >
                                <div className={styles.itemContent}>
                                    <h3 className={styles.itemName}>
                                        {typeof run.name === 'string' ? run.name : 'Без названия'}
                                    </h3>
                                    <div className={styles.info}>
                                        <span>ID:{run.id}</span>
                                        <CopyButton
                                            className={styles.btnCopyFactoryNumber}
                                            text={run.id}
                                            label="Копировать ID устройства"
                                        />
                                    </div>
                                    <div className={styles.itemDates}>
                                        <span className={styles.itemDate}>
                                            {formatTechnicalRunDate(run.dateStartTime)}
                                        </span>

                                        <span className={styles.itemDate}>
                                            {formatTechnicalRunDate(run.dateEndTime)}
                                        </span>
                                    </div>
                                </div>
                            </li>
                        ))}
                    </ul>
                )}
            </div>
        </div>
    );
}