import { useState, type ReactNode, createContext, useContext } from 'react';
import styles from './PasswordGate.module.css';

// Context для функции блокировки
interface PasswordGateContextValue {
    lock: () => void;
}

const PasswordGateContext = createContext<PasswordGateContextValue | null>(null);

export function usePasswordGate() {
    const context = useContext(PasswordGateContext);
    if (!context) {
        throw new Error('usePasswordGate must be used within PasswordGate');
    }
    return context;
}

interface PasswordGateProps {
    readonly children: ReactNode;
}

const DATABASES_PASSWORD = import.meta.env.VITE_DATABASES_PASSWORD || '0000';

export function PasswordGate({ children }: PasswordGateProps) {
    const [isUnlocked, setIsUnlocked] = useState(false);
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (password === DATABASES_PASSWORD) {
            setIsUnlocked(true);
            setError(false);
        } else {
            setError(true);
            setPassword('');
        }
    };

    const handleLock = () => {
        setIsUnlocked(false);
        setPassword('');
        setError(false);
    };

    if (isUnlocked) {
        return (
            <PasswordGateContext.Provider value={{ lock: handleLock }}>
                {children}
            </PasswordGateContext.Provider>
        );
    }

    return (
        <div className={styles.container}>
            <div className={styles.card}>
                <div className={styles.icon}>🔒</div>
                <h2 className={styles.title}>Доступ ограничен</h2>
                <p className={styles.description}>
                    Введите пароль для доступа к настройкам баз данных
                </p>

                <form onSubmit={handleSubmit} className={styles.form}>
                    <input
                        type="password"
                        className={`${styles.input} ${error ? styles.inputError : ''}`}
                        value={password}
                        onChange={(e) => {
                            setPassword(e.target.value);
                            setError(false);
                        }}
                        placeholder="Введите пароль"
                        autoFocus
                    />
                    {error && (
                        <span className={styles.errorText}>Неверный пароль</span>
                    )}
                    <button type="submit" className={styles.button}>
                        Войти
                    </button>
                </form>
            </div>
        </div>
    );
}
