import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useAppDispatch } from '@/store/hooks';
import { useConfirm } from '@ui/components/ConfirmProvider/ConfirmProvider';
import {
    fetchDatabases,
    createDatabase,
    updateDatabase,
    deleteDatabase,
    selectChartsMetaLoading,
    selectDatabases,
    selectDatabasesLoaded,
    selectErrors,
} from '@chartsPage/metaData/store/chartsMetaSlice';
import { useTestConnectionMutation } from '@chartsPage/metaData/shared/api/metadataApi';
import type { DatabaseDto } from '@chartsPage/metaData/shared/dtos/DatabaseDto';
import type { DbProviderType } from '@chartsPage/metaData/shared/dtos/types/DbProviderType';
import type { CreateDatabaseRequest } from '@chartsPage/metaData/shared/dtos/requests/CreateDatabaseRequest';
import type { UpdateDatabaseRequest } from '@chartsPage/metaData/shared/dtos/requests/UpdateDatabaseRequest';
import type { TestConnectionResponse } from '@chartsPage/metaData/shared/dtos/responses/TestConnectionResponse';
import { DatabaseAvailability } from '@chartsPage/metaData/shared/dtos/types/DatabaseAvailability';
import { DatabaseEditModal } from '@chartsPage/databases/ui/DatabaseEditModal/DatabaseEditModal';
import { usePasswordGate } from '@chartsPage/databases/ui/PasswordGate/PasswordGate';
import styles from './DatabasesPanel.module.css';

export function DatabasesPanel() {
    const dispatch = useAppDispatch();
    const confirm = useConfirm();
    const { lock } = usePasswordGate();

    const databases = useSelector(selectDatabases);
    const loading = useSelector(selectChartsMetaLoading).databases;
    const error = useSelector(selectErrors).databases;
    const databasesLoaded = useSelector(selectDatabasesLoaded);

    const [selectedDb, setSelectedDb] = useState<DatabaseDto | null>(null);
    const [editModalDb, setEditModalDb] = useState<DatabaseDto | null | 'create'>(null);
    const [isSaving, setIsSaving] = useState(false);

    useEffect(() => {
        if (!databasesLoaded && !loading) {
            dispatch(fetchDatabases({ force: false }));
        }
    }, [dispatch, databasesLoaded, loading]);

    const getProviderDisplayName = (provider: DbProviderType): string => {
        return String(provider);
    };

    const getAvailabilityStatus = (availability: DatabaseAvailability): { text: string; className: string } => {
        switch (availability) {
            case DatabaseAvailability.Online:
                return { text: 'Online', className: styles.statusOnline ?? '' };
            case DatabaseAvailability.Offline:
                return { text: 'Offline', className: styles.statusOffline ?? '' };
            default:
                return { text: String(availability), className: styles.statusUnknown ?? '' };
        }
    };

    const handleRefresh = () => {
        dispatch(fetchDatabases({ force: true }));
    };

    const handleCreate = () => {
        setEditModalDb('create');
    };

    const handleEdit = () => {
        if (selectedDb) {
            setEditModalDb(selectedDb);
        }
    };

    const handleDelete = async () => {
        if (!selectedDb) return;

        const ok = await confirm({
            title: 'Удалить базу данных?',
            description: `База данных "${selectedDb.name}" будет удалена. Это действие необратимо.`,
            confirmText: 'Удалить',
            cancelText: 'Отмена',
            danger: true,
        });

        if (ok) {
            setIsSaving(true);
            try {
                await dispatch(deleteDatabase({ id: selectedDb.id })).unwrap();
                setSelectedDb(null);
            } finally {
                setIsSaving(false);
            }
        }
    };

    const handleSave = async (data: CreateDatabaseRequest | UpdateDatabaseRequest) => {
        setIsSaving(true);
        try {
            if ('id' in data && data.id) {
                const updated = await dispatch(updateDatabase(data as UpdateDatabaseRequest)).unwrap();
                setSelectedDb(updated);
            } else {
                const created = await dispatch(createDatabase(data as CreateDatabaseRequest)).unwrap();
                setSelectedDb(created);
            }
            setEditModalDb(null);
        } finally {
            setIsSaving(false);
        }
    };

    const handleCloseModal = () => {
        setEditModalDb(null);
    };

    if (loading && !databasesLoaded) {
        return (
            <div className={styles.container}>
                <div className={styles.loading}>Загрузка баз данных...</div>
            </div>
        );
    }

    if (error) {
        return (
            <div className={styles.container}>
                <div className={styles.error}>
                    <span>Ошибка: {error}</span>
                    <button onClick={handleRefresh} className={styles.retryButton}>
                        Повторить
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className={styles.container}>
            <div className={styles.header}>
                <h2 className={styles.title}>Базы данных</h2>
                <div className={styles.headerActions}>
                    <button
                        onClick={handleCreate}
                        className={styles.createButton}
                        disabled={isSaving}
                    >
                        + Создать
                    </button>
                    <button
                        onClick={handleRefresh}
                        disabled={loading || isSaving}
                        className={styles.refreshButton}
                    >
                        {loading ? 'Загрузка...' : 'Обновить'}
                    </button>
                    <button
                        onClick={lock}
                        className={styles.lockButton}
                    >
                        Заблокировать
                    </button>
                </div>
            </div>

            <div className={styles.content}>
                {/* Список баз данных */}
                <div className={styles.listPanel}>
                    <div className={styles.listHeader}>
                        <span>Доступные базы данных ({databases.length})</span>
                    </div>
                    <div className={styles.list}>
                        {databases.length === 0 ? (
                            <div className={styles.emptyList}>
                                Нет доступных баз данных
                            </div>
                        ) : (
                            databases.map((db) => {
                                const status = getAvailabilityStatus(db.availability);
                                return (
                                    <div
                                        key={db.id}
                                        className={`${styles.listItem} ${selectedDb?.id === db.id ? styles.listItemSelected : ''}`}
                                        onClick={() => setSelectedDb(db)}
                                    >
                                        <div className={styles.listItemMain}>
                                            <span className={styles.dbName}>{db.name}</span>
                                            <span className={`${styles.status} ${status.className}`}>
                                                {status.text}
                                            </span>
                                        </div>
                                        <div className={styles.listItemSub}>
                                            <span className={styles.provider}>
                                                {getProviderDisplayName(db.provider)}
                                            </span>
                                        </div>
                                    </div>
                                );
                            })
                        )}
                    </div>
                </div>

                {/* Детали выбранной базы данных */}
                <div className={styles.detailsPanel}>
                    {selectedDb ? (
                        <DatabaseDetails
                            database={selectedDb}
                            getProviderDisplayName={getProviderDisplayName}
                            getAvailabilityStatus={getAvailabilityStatus}
                            onEdit={handleEdit}
                            onDelete={handleDelete}
                            isLoading={isSaving}
                        />
                    ) : (
                        <div className={styles.noSelection}>
                            Выберите базу данных для просмотра деталей
                        </div>
                    )}
                </div>
            </div>

            {/* Модальное окно редактирования */}
            {editModalDb !== null && (
                <DatabaseEditModal
                    database={editModalDb === 'create' ? null : editModalDb}
                    onCancel={handleCloseModal}
                    onSave={handleSave}
                    isLoading={isSaving}
                />
            )}
        </div>
    );
}

interface DatabaseDetailsProps {
    readonly database: DatabaseDto;
    readonly getProviderDisplayName: (provider: DbProviderType) => string;
    readonly getAvailabilityStatus: (availability: DatabaseAvailability) => { text: string; className: string };
    readonly onEdit: () => void;
    readonly onDelete: () => void;
    readonly isLoading?: boolean;
}

function DatabaseDetails({
    database,
    getProviderDisplayName,
    getAvailabilityStatus,
    onEdit,
    onDelete,
    isLoading,
}: DatabaseDetailsProps) {
    const status = getAvailabilityStatus(database.availability);

    const [testConnection, { isLoading: isTesting }] = useTestConnectionMutation();
    const [testResult, setTestResult] = useState<TestConnectionResponse | null>(null);

    // Сбрасываем результат при смене базы данных
    useEffect(() => {
        setTestResult(null);
    }, [database.id]);

    const handleTestConnection = async () => {
        // Сбрасываем результат перед новой попыткой
        setTestResult(null);

        try {
            const result = await testConnection({
                connectionString: database.connectionString,
                provider: database.provider,
            }).unwrap();
            setTestResult(result);
        } catch {
            setTestResult({
                success: false,
                serverVersion: null,
                errorMessage: 'Не удалось выполнить запрос',
                responseTimeMs: 0,
            });
        }
    };

    return (
        <div className={styles.details}>
            <div className={styles.detailsHeader}>
                <h3 className={styles.detailsTitle}>{database.name}</h3>
                <div className={styles.detailsActions}>
                    <button
                        className={styles.testButton}
                        onClick={handleTestConnection}
                        disabled={isLoading || isTesting}
                    >
                        {isTesting ? 'Проверка...' : 'Проверить подключение'}
                    </button>
                    <button
                        className={styles.editButton}
                        onClick={onEdit}
                        disabled={isLoading}
                    >
                        Редактировать
                    </button>
                    <button
                        className={styles.deleteButton}
                        onClick={onDelete}
                        disabled={isLoading}
                    >
                        Удалить
                    </button>
                </div>
            </div>

            {/* Test result - контейнер с фиксированной высотой */}
            {(isTesting || testResult) && (
                <div className={styles.testResultContainer}>
                    {isTesting ? (
                        <div className={styles.testLoading}>
                            Проверка подключения...
                        </div>
                    ) : testResult && (
                        <div className={testResult.success ? styles.testSuccess : styles.testError}>
                            {testResult.success ? (
                                <>
                                    <span className={styles.testIcon}>✓</span>
                                    <div className={styles.testMessage}>
                                        <strong>Подключение успешно</strong>
                                        {testResult.serverVersion && (
                                            <span>Версия сервера: {testResult.serverVersion}</span>
                                        )}
                                        <span>Время ответа: {testResult.responseTimeMs} мс</span>
                                    </div>
                                </>
                            ) : (
                                <>
                                    <span className={styles.testIcon}>✕</span>
                                    <div className={styles.testMessage}>
                                        <strong>Ошибка подключения</strong>
                                        {testResult.errorMessage && (
                                            <span>{testResult.errorMessage}</span>
                                        )}
                                        {testResult.responseTimeMs > 0 && (
                                            <span>Время ответа: {testResult.responseTimeMs} мс</span>
                                        )}
                                    </div>
                                </>
                            )}
                        </div>
                    )}
                </div>
            )}

            <div className={styles.detailsGrid}>
                <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>Статус:</span>
                    <span className={`${styles.status} ${status.className}`}>
                        {status.text}
                    </span>
                </div>

                <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>Провайдер:</span>
                    <span className={styles.detailValue}>
                        {getProviderDisplayName(database.provider)}
                    </span>
                </div>

                <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>Версия:</span>
                    <span className={styles.detailValue}>
                        {database.databaseVersion || 'Не указана'}
                    </span>
                </div>

                {database.description && (
                    <div className={styles.detailRow}>
                        <span className={styles.detailLabel}>Описание:</span>
                        <span className={styles.detailValue}>{database.description}</span>
                    </div>
                )}

                <div className={styles.detailRowFull}>
                    <span className={styles.detailLabel}>Строка подключения:</span>
                    <div className={styles.connectionString}>
                        <code>{database.connectionString}</code>
                    </div>
                </div>

                {database.lastConnectivityAt && (
                    <div className={styles.detailRow}>
                        <span className={styles.detailLabel}>Последнее подключение:</span>
                        <span className={styles.detailValue}>
                            {new Date(database.lastConnectivityAt).toLocaleString('ru-RU')}
                        </span>
                    </div>
                )}

                {database.LastConnectivityError && (
                    <div className={styles.detailRowFull}>
                        <span className={styles.detailLabel}>Ошибка подключения:</span>
                        <div className={styles.errorMessage}>
                            {database.LastConnectivityError}
                        </div>
                    </div>
                )}

                <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>Таблиц:</span>
                    <span className={styles.detailValue}>
                        {database.entities?.length ?? 0}
                    </span>
                </div>

                <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>Шаблонов:</span>
                    <span className={styles.detailValue}>
                        {database.chartReqTemplates?.length ?? 0}
                    </span>
                </div>
            </div>
        </div>
    );
}
